.img {
  display: block;
}

.App {
  background: linear-gradient(132deg, #14113b,#0e0c33, #120b21,#140b21,#0e0c33,#14113b);
  /*background:rgb(19, 19, 62);*/
  background-size: 400% 400%;
  animation: BackgroundGradient 40s ease infinite;
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.footer {
  width: 100vw;
  height: 1in;
  /*background-image: linear-gradient(to right, rgb(221, 221, 221) , rgb(0, 0, 0));*/
  background:white;
}

@keyframes BackgroundGradient {
  0% {background-position: 0% 50%;}
  50% {background-position: 100% 50%;}
  100% {background-position: 0% 50%;}
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 50s linear;
  }
}

@media (max-width: 1400px) {
  .hide-on-small {
    display: none;
  }
  .main {
    width:100%;
    height:100%;
    float:right;
    color:white;
    font:'lexend';
    font-size: 25px;
  }
}

@media (min-width: 1400px) {
  .main {
    width:70%;
    height:100%;
    float:right;
    color:white;
    font:'lexend';
    font-size: 25px;
  }
}
.Header {
  /*background-image: linear-gradient(to right, rgb(221, 221, 221) , rgb(0, 0, 0));*/
  background:white;
  display: flex;
  height: .5in;
  position: sticky;
  z-index: 1000;
  align-items: center;
  padding:1%;
  top: 0;
  box-shadow: rgba(0, 0, 0, 0.4) 0 2px 4px,rgba(0, 0, 0, 0.3) 0 7px 13px -3px,rgba(0, 0, 0, 0.5) 0 -3px 0;
}

.image {
  display: flex;
  justify-content: center;
  position:absolute;
  width:98%;
  height: 75%;
  margin:auto;
  overflow-y: auto;
  overflow-x:hidden;
  padding:1%;
}

.grid {
  display:grid;
  align-items: center;
  grid-template-columns: auto auto auto;
  gap: .2in;
}

.HeaderStart {
  display: flex;
  justify-content:flex-start;
  gap: 5px;
  position: relative;
  margin: 0;
  width:50%;
  float:left;
}

.HeaderEnd {
  display: flex;
  justify-content:flex-end;
  gap: 5px;
  position: relative;
  margin: 0;
  color:rgb(0, 0, 0);
  width:50%;
  float:right;
}

.button {
  margin: 0;
  align-items: center;
  appearance: none;
  background: rgb(18, 18, 18);
  border: 0;
  box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: "JetBrains Mono",monospace;
  height: 48px;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow .15s,transform .15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow,transform;
  font-size: 18px;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
